import React from 'react'
import '../css/Requestmodal.css';
import { Button, Icon, Modal } from 'semantic-ui-react'



const Requestmodal = () => {
  const [open, setOpen] = React.useState(false)

  return (
    <Modal
      open={open}
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
      trigger={<button className="btn_request ui large button"> <Icon name='chevron right' /></button>}
    >
      <Modal.Header className="modal-title">  </Modal.Header>
      <Modal.Content>
          <p>
              
          </p>
      </Modal.Content>
      <Modal.Actions>
        <Button onClick={() => setOpen(false)} className="request-btn">
           <Icon name='chevron right' />
        </Button>
      </Modal.Actions>
    </Modal>
  )
}

export default Requestmodal;