import React from 'react';
import '../css/ProgressIcons.css';
import ProgressIconBox from "../Components/ProgressIconBox";
import Progress1 from '../image/progress1.png';
import Progress2 from '../image/progress2.png';
import Progress3 from '../image/progress3.png';
import Progress4 from '../image/progress4.png';
import Progress5 from '../image/progress5.png';

function ProgressIcons() {
  return (
      <div className="progressicons-container">
                <ProgressIconBox className="margintop" img={Progress1} title=" " 
                  description="/PC   <br/>
                   <br/>
                   /  "     
                 em=" "></ProgressIconBox>
                <ProgressIconBox img={Progress2} title=" " 
                  description=",   <br/> 
                  9 150 "
                  em=" "></ProgressIconBox>
                <ProgressIconBox  className="margintop" img={Progress3} title=" " 
                  description=" Data Scraping <br/>
                   ,<br/>
                     <br/>
                  + "em=" "></ProgressIconBox>
                <ProgressIconBox img={Progress4} title=" " 
                  description=" (//)<br/>
                     (by)"em=" "></ProgressIconBox>
                <ProgressIconBox className="margintop" img={Progress5} title=" " 
                  description=", ,   <br/>
                      <br/>
                  ()  "em=" "></ProgressIconBox>
      </div>
  );
}

export default ProgressIcons;