import React from 'react';
import Footer from './Footer';
import Termmodal from '../Components/Termmodal';
import '../css/Contact.css';

function Contact(){
    return(
        <>
        <div className="contact-container">
            <h1></h1>
            <p>    !</p>
            <form className="contact-form" id="contact">
                <div className="form-container">
                    <div className="form-input"><label for="input-name">/</label><input id="input-name" type="text" name="name"/></div>
                    <div className="form-input"><label for="input-call"></label><input id="input-call" type="text" name="call"/></div>
                    <div className="form-input"><label for="input-email"></label><input id="input-email" type="text" name="email"/></div>
                    <div className="form-input">
                        <label for="input-sort"></label>
                        <div className="select">
                            <select id="input-sort" name="sort">
                                <option value="" selected disabled hidden ></option>
                                <option value="program"></option>
                                <option value="progress"></option>
                                <option value="service"></option>
                                <option value="error"></option>
                                <option value="etc"></option>
                            </select>
                            <div className="select-arrow"></div>
                        </div>
                    </div>
                    <div className="form-input"><label for="input-content"></label><input id="input-content" type="text" name="contetent"/></div>
                    <p className="term">
                        <input id="checkbox" className="checkbox"type="checkbox"></input>
                        <Termmodal/>
                    </p>
                </div>
            </form>
            <button className="submit-btn" type="submit" form="contact"></button>
            
        </div>
        <Footer></Footer>
        </>
    );
}

export default Contact;